import { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";

import "../styles/Home.css";

function Home() {
  const [templates, setTemplates] = useState([]);

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/templates/}`)
      .then((res) => {
        setTemplates(res.data);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className="navbar">
      <h1>My Templates</h1>
      <div className="card mb-3">
        {templates.map((template) => (
          <div key={template.id}>
            <img
              src={template.image}
              alt={template.title}
              className="card-img-top"
            />
            <div className="card-body">
              <h3 calss>{template.title}</h3>
              <p className="card-text">{template.description}</p>
              <p className="card-text">
                {" "}
                <small className="text-body-secondary">
                  {" "}
                  Category: {template.category}{" "}
                </small>{" "}
              </p>
              <p className="card-text">
                {" "}
                <small className="text-body-secondary">
                  Created At: {template.created_at}
                </small>{" "}
              </p>
              <a
                className="btn btn-primary"
                href={`${process.env.REACT_APP_API_URL}/media/templates/my-commerce.rar}`}
              >
                Get Template
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Home;
